<template>
    <Table :columns="columns" :data="data"></Table>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '支付单号',
                        key: 'trade_no'
                    },
                    {
                        title: '充值金额',
                        key: 'recharge_amount'
                    },
                    {
                        title: '充值方式',
                        key: 'recharge_way'
                    },
                    {
                        title: '支付时间',
                        key: 'timestamp'
                    },
                    {
                        title: '支付状态',
                        key: 'trade_status'
                    }
                ],
                data: [
                ]
            }
        },
        mounted() {
            axios.get("http://"+location.hostname+"/home/getOrderByUser").then(data => {
                if(data.data.code==1) {
                    this.data = data.data.data.map(item => Object.assign(item,{
                        trade_status: item.trade_status ? "已付款" : "未付款"
                    }));
                } else {
                    console.error(data.data.msg);
                }
             });
        }
    }
</script>